import React, { Component } from "react";
import { TextInput, Field } from "components/Form";
import { mdiAccount, mdiEmail } from "@mdi/js";
import Card from "components/Card";

export default class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      name: "",
      message: "",
      disabled: true,
      validEmail: true,
      success: false
    };
  }

  validate() {
    let { email, name, message } = this.state;
    let valid = (email !== "" && name !== "" && message !== "")
    this.setState({ disabled: !valid, })
    if (email !== "") {
      this.setState({ validEmail: this.isEmail(email) })
    }
  }

  isEmail(str) {
    let regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return regex.test(String(str).toLowerCase())
  }

  handleSubmit() {
    this.setState({ success: true });
    setTimeout(() => {
      this.setState({ success: false });
    }, 2000);
  }
  render() {
    return (
      <>
        <div className="section-light contact my-component" id="contact">
          <div
            className="columns is-multiline"
            data-aos="slide-up"
            data-aos-duration="500"
            data-aos-easing="linear"
          >
            <Card width={12}>
              <div className="column is-12 about-me">
                <h1 className="title has-text-centered section-title">
                  Get in touch
                </h1>
              </div>
              <div className="column is-6 is-offset-3">
                {!this.state.validEmail && <div className="notification is-danger is-light">
                  Invalid email.
              </div>}
                {this.state.success && <div className="notification is-success is-light">
                  Thank you for contacting me. I'll get back to you soon :)
              </div>}
                <TextInput
                  label="Name*"
                  placeholder="Ex. John Doe"
                  value={this.state.name}
                  isIcon={{ show: true, name: mdiAccount, float: "left" }}
                  onChange={e => {
                    this.validate()
                    this.setState({ name: e.target.value })
                  }}
                />
                <TextInput
                  label="Email*"
                  type="email"
                  placeholder="Ex. johndoe@email.com"
                  value={this.state.email}
                  isIcon={{ show: true, name: mdiEmail, float: "left" }}
                  onChange={(e) => {
                    this.validate()
                    this.setState({ validEmail: false, email: e.target.value })
                  }}
                />
                <Field>
                  <label className="label">Message*</label>
                  <div className="control">
                    <textarea
                      onChange={(e) => {
                        this.setState({ message: e.target.value })
                        this.validate()
                      }}
                      className="textarea"
                      placeholder="Textarea"
                      name="Message"
                    ></textarea>
                  </div>
                </Field>
                <Field>
                  <div className="control ">
                    <button onClick={() => {
                      this.setState({
                        email: "",
                        name: "",
                        message: "",
                        disabled: true,
                        validEmail: true,
                        success: true
                      })
                      setTimeout(() => {
                        this.setState({ success: false })
                      }, 2000);
                    }} disabled={this.state.email === "" || (this.state.disabled && !this.state.validEmail)} className="button submit-button">
                      Submit&nbsp;&nbsp;
                      <i className="fas fa-paper-plane"></i>
                    </button>
                  </div>
                </Field>
              </div>
            </Card>
          </div>
        </div>
      </>
    );
  }
}
