import React, { useEffect } from "react";
import bulmaCarousel from "bulma-carousel/dist/js/bulma-carousel.min.js";

export default function Technology() {
  useEffect(() => {
    bulmaCarousel.attach(".hero-carousel", {
      slidesToScroll: 2,
      slidesToShow: 4,
      infinite: 1,
      pagination: 0,
      autoPlay: 0
    });
  }, [])
  let skills = [
    "html.svg",
    "css.svg",
    "javascript.png",
    "nodeJS.png",
    "jquery.svg",
    "php.svg",
    "reactJS.png",
    "vueJS.png",
    "laravel.png",
    "asp.net.svg",
    "mySQL.svg",
    "mongoDB.png",
    "bootstrap.svg",
    "sass.svg",
    "android.png",
  ];
  return (
    <div className="section my-component" id="skills"
      data-aos="zoom-in"
      data-aos-duration="1000"
      data-aos-mirror="true"
      data-aos-once="false">

      <div className="container">


        <div
          className="columns is-multiline"
          data-aos="fade-in"
          data-aos-easing="linear"
        >
          <div className="column is-12">
            <h1 className="title  has-text-centered section-title">
              My Technologies
              </h1>
          </div>

          <div className="column is-12">

            <div className="section hero is-small has-carousel _hero">
              <br />
              <br />
              <br />
              <div className="hero-carousel">
                {skills.map((logo, idx) => {
                  return (
                    <figure
                      className="image is-96x96 mx-5 my-5 mt-10"
                    >
                      <img src={require(`assets/img/logo/${logo}`)} alt={logo} />
                    </figure>
                  );
                })}
              </div>
            </div>

          </div>
        </div>
        <br />
        <br />
      </div>
    </div>
  );
}
